<template>
  <v-switch 
    :rules="rules" 
    v-model="content" 
    v-bind="$attrs" 
    :color="$vuetify.theme.currentTheme.primary"
    v-on="$listeners"
  >
    <template 
      v-if="$attrs.tooltip"
      v-slot:append 
    >
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-icon v-on="on" style="cursor: pointer">mdi-help-circle-outline</v-icon>
        </template>
        {{ $attrs.tooltip }}
      </v-tooltip>
    </template>
     <template 
        v-if="$attrs.required" 
        v-slot:label   
     >
        {{$attrs.label}} <span class="red--text">*</span>
    </template>
  </v-switch>
</template>

<script>
import { ednVModel } from './mixins/ednVModel'
import { ednRequired } from './mixins/ednRequired'

export default {
  mixins: [ednVModel,ednRequired],
  inheritAttrs: false,
  props: {
    invalidMsg: {
      type: String,
      default: () => 'Merci de bien vouloir activer le switch',
    },
  },
}
</script>
