//Mixin des comportement mimic du nouveau mode fiche (iris)

export const ednMimicMix = {
  computed: {
    getAppendedIcon() {
      if (this.getIrisRdonlyMode) return 'mdi-lock'
      else if ((this.$attrs.irisMimic === '' || this.$attrs.irisMimic) && this.content && this.content.length > 0)
        return 'mdi-pencil'
    },
    getIrisRdonlyMode() {
      if (this.$attrs.rdonly === '' || this.$attrs.rdonly) {
        return this.$attrs.rdonly === '' || this.$attrs.rdonly
      }
    },
    getIrisMode() {
      let irisMimic = this.$attrs.irisMimic || this.$attrs["iris-mimic"]
      if (irisMimic || this.irisMimic) return irisMimic === '' || irisMimic || this.irisMimic
    },
    /**
     * Récupère le nom du slot et place le tooltip
     * différemment si on a le cadenas ou pas
     */
    getSlotName() {
      return this.getAppendedIcon ? 'append-outer' : 'append'
    },
    getIrisCssClass() {
      return this.getIrisMode ? 'iris-mimic' : this.getIrisRdonlyMode ? 'iris-rdonly' : ''
    },
  },
  methods: {
    focusOnInput() {
      if (this.$refs.input) this.$refs.input.focus()
    },
  },
}
