<template>
  <edn-field
    v-if="!(irisMimic || getIrisRdonlyMode)"
    ref="edn_mail"
    @update:error="isReady($event)"
    v-model="content"
    v-bind="$attrs"
    v-on="$listeners"
    :pattern="pattern"
    :invalidMsg="invalidMsg"
  >
  </edn-field>
  <edn-mimic :rules="rules" v-bind="$attrs" :pattern="pattern" v-model="content" type="mail" v-else />
</template>

<script>
import { ednRequired } from './mixins/ednRequired'
import { ednVModel } from './mixins/ednVModel'
import ednMimic from './ednMimic'
import { ednMimicMix } from './mixins/ednMimicMix'
import { ednRegex } from './mixins/ednRegex'

export default {
  components: {
    ednMimic,
  },
  props: {
    irisMimic: {
      type: Boolean,
      default: () => false,
    },
    pattern: {
      type: RegExp,
      default: () =>
        /^$|^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)+$/i,
    },
  },
  data() {
    return {
      linkContent: '',
      readyToConvert: null,
      linkMode: false,
    }
  },
  mixins: [ednRequired, ednVModel, ednRegex, ednMimicMix],
  inheritAttrs: false,
  methods: {},
}
</script>

<style lang="scss">
div {
  &.v-input {
    &.v-text-field {
      &.linkIris {
        div {
          &.v-input__slot {
            :after {
              border: none;
            }
          }
        }
        input {
          text-decoration: underline;
          color: #1ca6fd;
          cursor: pointer;
        }
      }
    }
  }
}
</style>
