<template>
  <v-checkbox 
    v-model="content" 
    v-bind="$attrs" 
    :color="$vuetify.theme.currentTheme.primary" 
    :rules="rules"
    v-on="$listeners"
  >
    <template 
      v-if="$attrs.tooltip"
      v-slot:append
    >
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-icon 
            v-on="on" 
            style="cursor: pointer"
          >mdi-help-circle-outline</v-icon>
        </template>
        {{ $attrs.tooltip }}
      </v-tooltip>
    </template>
     <template 
      v-if="$attrs.required" 
      v-slot:label  
     >
        {{$attrs.label}} <span class="red--text">*</span>
    </template>
  </v-checkbox>
</template>

<script>
import { ednRequired } from './mixins/ednRequired'
import { ednVModel } from './mixins/ednVModel'

export default {
    inheritAttrs: false,
    mixins: [ednRequired, ednVModel],
}
</script>
