<template>
  <edn-field
    autocomplete="false"
    type="text"
    ref="input"
    @keyup="textFilter($event)"
    v-model="content"
    v-bind="$attrs"
    :rules="rules"
    :invalidMsg="invalidMsg"
    v-on="$listeners"
 >

  </edn-field>
</template>

<script>
import { ednRequired } from './mixins/ednRequired'
import { ednVModel } from './mixins/ednVModel'


export default {
  components: {
  },
  mixins: [ednVModel,ednRequired],
  props: {
    pattern: {
      type: RegExp,
      default: () => /[^.\d ]/gu,
    },
     invalidMsg: {
      type: String,
      default: () => '',
    },
    value: null,
  },
  data() {
    return {
      content: this.value,
      preValue: null,
      filteredValue: null,
    }
  },
  inheritAttrs: false,
  methods: {
    /* eslint-disable no-unused-vars */
    textFilter(e) {
      // debugger
      if (this.content[0] === '-' || this.content[0] === '+') {
        this.content = this.content[0] + this.content.slice(1).replaceAll(this.pattern, '')
        // if (this.content.includes(',')) {
        // }
        this.$emit('input', this.content)
      } else {
        this.content = this.content.replaceAll(this.pattern, '')
      }
      this.$emit('input', this.content)
    },
  },
  /* eslint-disable no-unused-vars */

  watch: {
    value() {
      if (this.value != this.content) {
        this.content = this.value
      }
    },
  },
}
</script>
