<template>
  <edn-skeleton-template type="field" v-if="$attrs.isLoading"></edn-skeleton-template>
  <v-text-field
    v-else
    autocomplete="off"
    spellcheck="false"
    v-on="$listeners"
    v-bind="$attrs"
    type="text"
    v-model="content"
    :rules="rules"
    @click:append="focusOnInput"
    :append-icon="getAppendedIcon"
    :disabled="getIrisRdonlyMode || $attrs.disabled"
    :filled="$attrs.filled || getIrisRdonlyMode "
    :class="[getIrisCssClass,getTextTransform]"
  >
    <template v-slot:[getSlotName] v-if="$attrs.tooltip">
      <edn-tooltip> {{ $attrs.tooltip }}</edn-tooltip>
    </template>
    <template v-slot:label>
      <div v-if="fldRequiredVal && !$attrs.readonly">{{ $attrs.label }} <span class="red--text">*</span></div>
    </template>
    <template v-slot:append-outer><slot v-bind:url="$attrs.url" name="edn-append-outer"></slot></template>
  </v-text-field>
</template>

<script>
import { ednVModel } from './mixins/ednVModel'
import { ednRegex } from './mixins/ednRegex'
import { ednRequired } from './mixins/ednRequired'
import { ednMimicMix } from './mixins/ednMimicMix'
import ednTooltip  from './ednTooltip'
import { ednTransform } from './mixins/ednTransform'

export default {
  inheritAttrs: false,
  data(){
    return{
      aRules:[],
    }
  },
  watch:{
    required:function(val){
      this.$emit('requiredChange',val)
    }
  },
  props: {
    pattern: {
      type: RegExp
    },  
    required:{
      type:[String, Boolean],
      default : ()=> false
    }
  },
  components: {
    ednTooltip,
  },
  mixins: [ednMimicMix, ednVModel, ednTransform, ednRequired,ednRegex],
  mounted() {
    this.aRules = this.rules;
  },
  updated() {
    this.aRules = this.rules;
  },
}
</script>
