<template>
    <component
        :is="logicType ? 'edn-switch' : 'edn-check'"
        v-model="content"
        :label="label"
        :readonly="readonly"
        :required="required"
        :tooltip="tooltip"
        :disabled="disabled"
        v-on="$listeners"
    />
</template>

<script>
import { ednVModel } from './mixins/ednVModel'
import { ednRequired } from './mixins/ednRequired'

export default {
    name: 'IrisEdnLogic',
    mixins: [
        ednVModel,
        ednRequired
    ],
    inheritAttrs: false,
    props: {
        logicType: {
            type: Number,
            default: 0
        },
        label: {
            type: String
        },
        readonly: {
            type: Boolean,
            default: false
        },
        required: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean
        },
        tooltip: {
            type: Object
        }
    }
}
</script>