<template>
    <edn-field
      :irisMimic="irisMimic"
      ref="edn_phone"
      @update:error="isReady($event)"
      v-model="content"
      @blur="isMimic"
      v-bind="$attrs"
      v-on="$listeners"
      v-if="!irisMimic"
      :pattern="pattern"
      :invalidMsg="invalidMsg"
    >
    </edn-field>
    <edn-mimic v-else :rules="rules" v-bind="$attrs" :pattern="pattern" v-model="content" type="phone"  />
</template>

<script>
import { ednRequired } from './mixins/ednRequired'
import { ednVModel } from './mixins/ednVModel'
import ednMimic from './ednMimic'
import { ednRegex } from './mixins/ednRegex'

export default {
  components: {
    ednMimic,
  },
  props: {
    irisMimic: {
      type: Boolean,
      default: () => false,
    },
    // invalidMsg: {
    //   type: String,
    //   default: () => 'Vous devez entrer un numéro de téléphone valide.',
    // },
    pattern: {
      type: RegExp,
      /*eslint no-useless-escape: "error"*/
      default: () => /^$|^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/im,
      /*eslint no-useless-escape: "error"*/
    },
  },
  data() {
    return {
      linkContent: '',
      readyToConvert: true,
      linkMode: false,
    }
  },
  mixins: [ednRequired, ednVModel,ednRegex],
  inheritAttrs: false,
  methods: {
    changeToLink() {
      this.linkContent = this.content
    },
    isReady(e) {
      this.readyToConvert = !e
    },
    isMimic() {
      if (this.content) this.linkMode = this.irisMimic && this.content.length > 0 && this.readyToConvert
    },
    exit(e) {
      this.linkMode = e
    },
  },

  
}
</script>
